@import "../utils/variables";

@mixin clock () {
  display: flex;
  justify-content: center;
  align-items: center;
  width: $clock-size;
  height: $clock-size;
  border-radius: 50%;
}

@mixin clock-center {
  display: flex;
  justify-content: center;
  align-items: center;
  width: $center-size;
  height: $center-size;
  border-radius: 50%;
}
